


























































export default {
	data() {
		return {
			phone : '18888888888',
			verificationCode : '',
			array: ['1', '10', '100'],
			index: 0,
			isDisplayVerificationCode : false, //是否显示验证码这一栏,默认不显示
			phoneTxt : '修改',
			codeTime: '', //默认信息反应的时间为60s
			waitCode : '', //给用户发送的短信验证码
			vertifyTxt : '',
		};
	},
	methods: {
		clickFront() {
			console.log('点击了正面');
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				// sourceType: ['album'], //从相册选择
				success: function (res) {
					console.log(JSON.stringify(res));
					var tempFilePaths = res.tempFilePaths;
					 uni.saveFile({
					  tempFilePath: tempFilePaths[0],
					  success: function (res) {
						  console.log('保存返回的数据',res);
						var savedFilePath = res;
					  }
					});
				}
			});
		},
		clickBack() {
			console.log('点击了反面');
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				// sourceType: ['album'], //从相册选择
				success: function (res) {
					console.log(JSON.stringify(res.tempFilePaths));
				}
			});
		},
		bindPickerChange: function(e) {
			console.log('picker发送选择改变，携带值为', e.target.value)
			this.index = e.target.value
		},
		changePhone(e) {
			// 判断点击的是 修改 还是 获取验证码
			if(this.phoneTxt == '修改') {
				// 修改手机号,要显示验证码这一栏
				this.isDisplayVerificationCode = true;
				this.phoneTxt = '获取验证码';
				console.log('修改号码');
			} else if(this.phoneTxt == '获取验证码' || this.phoneTxt == '重新获取验证码') {
				if(this.phone.length != 11) {
					return false;
				}
				// 需要给用户的手机号发送短信
				let code = global.getVeritifyCode(); // 这个函数在全局
				let mobile = this.phone; //将要绑定的手机号,即发送的手机号
				let tpl_id = 140513; //模板号 模板就是发送信息的模板,模板是聚合数据的模板
				let tpl_value = '%23code%23%3D' + code;
				let key = '8972e94284e4fb4fafc3266c8834d25c'; //聚合数据的appkey
				let url = "http://v.juhe.cn/sms/send?mobile=" + mobile + '&tpl_id=' + tpl_id + '&dtype=&key=' +  key + '&tpl_value=' + tpl_value; 
				uni.request({
					url: url, 
					success: (res) => {
						if(res.data.reason == "操作成功") {
							this.waitCode = code; 
							// 信息发送成功
							this.codeTime = 60;
							this.phoneTxt = '后重新获取';
							var timer = setInterval(()=> {
								this.codeTime--;
								if(this.codeTime  <= 0) {
									console.log('99',this.codeTime);
									clearInterval(timer);
									this.codeTime = '';
									this.phoneTxt = '重新获取验证码';
								}
							},1000);
						}
					}
				});
			}
			
		},
		// 验证手机验证码
		vertifyCode(e) {
			// 判断用户输入的验证码与发送的验证码是否一致
			if(e.detail.value == this.waitCode && e.detail.value !='') {
				this.vertifyTxt = '验证成功';
				this.codeTime = 0;
				// this.phoneTxt = '重新取验证码';
				uni.showToast({
					title: '修改成功了',
					duration: 2000
				});
				try {
					const openid = uni.getStorageSync('openid');
					if (openid) {
						uni.request({
							url: global.host + 'Zhu/changePhone',
							method: 'GET',
							data: {
								openid : openid,
								user_phone : this.phone
							},
							success: res => {
								console.log('修改手机号成功',res);
								setTimeout(() => {
									this.isDisplayVerificationCode  = false;
									this.phoneTxt = '修改';
									this.vertifyTxt = '';
									this.verificationCode = '';
								},2000)
							},
							fail: () => {},
							complete: () => {}
						});
					}
				} catch (e) {
					// error
				}
			} else if(this.phoneTxt == '获取验证码'){
				uni.showToast({
					title: '请先获取验证码',
					duration: 2000,
					icon :'none'
				});
			} else {
				this.vertifyTxt = '验证码错误';
			}
		},
		goRegistrationInfo() {
			uni.navigateTo({
				url: '../registrationInfo/registrationInfo'
			});
		},
		vertifyPhone(e) {
			let phone = e.detail.value;
			if(phone.length != 11) {
				uni.showToast({
					title: '手机号不正确',
					duration: 2000,
					icon :'none'
				});
			} else {
				console.log('89',e.detail.value);
				this.phone = e.detail.value;
			}
		}
	}
}
